  export default {
	data (){
		return {
			arr: [],
			list: [

			]
        }
	},
	computed: {
		// 总数计算
		totalnum (){
			let result = 0;
			for (var i = 0; i < this.list.length; i++) {
				if (this.list[i].selected && this.list[i].num) {
					result += Number.parseInt(this.list[i].num);
					this.list[i].total = this.arr[i] * Number.parseInt(this.list[i].num);
				}
			}
			return result;
		},
		// 总价格计算
		totalmoney (){
			let result = 0;
			for (var i = 0; i < this.list.length; i++) {
				if (this.list[i].selected) {
					result += Number.parseFloat(this.list[i].total);
				}
			}
			return result;
		},
		// 检测全选
		checkSel (){
			for (var i = 0; i < this.list.length; i++) {
				if (this.list[i].selected == true) {
					return true;
				}else{
					return false;
				}
			}
		},
		// 从状态取内容
		getter (){
			this.list = this.$store.getters.getCart;
			for (var i = 0; i < this.list.length; i++) {
				this.arr.splice(i,1);
				this.arr.push(this.list[i].total);
			}
		}
	},
	methods: {
		// 数量减
		reduce (index){
			if (this.list[index].num > 1) {
				this.list[index].num --;
				this.list[index].total = this.arr[index] * Number.parseInt(this.list[index].num);
			}
		},
		// 数量加
		add (index){
			if (this.list[index].num < 10) {
				this.list[index].num ++;
				this.list[index].total = this.arr[index] * Number.parseInt(this.list[index].num);
			}
		},
		// 选择选中
		select (index,ev){
			var ev = window.event || ev;
			if (ev.target.className == 'add' || ev.target.className == 'remove' || ev.target.className == 'reduce' || ev.target.className == 'num') {
				return;
			}else{
				this.list[index].selected = !this.list[index].selected;
			}
		},
		// 删除
		remove (index){
			this.$store.dispatch('delate_cart',index);
			alert('删除成功！');
		},
		// 全选
		checkbox (){
			for (var i = 0; i < this.list.length; i++) {
				this.list[i].selected = !this.list[i].selected;
			}
		}
	},
	created (){
		this.getter;
	},
    watch: {
        $route(){
        	this.getter;
        }
    },
  }



